<template>
  <section :style="dynamicDiv" class="d-flex flex-column justify-content-center bg-primary text-light">
    <div class="body">
      <h1 class="my-3">Decentralized financial platform for price-stable cryptocurrencies</h1>
      <!-- md, lg, xl -->
      <div class="d-none d-md-flex justify-content-center">
        <a :href="subscriptionUrl" target="_blank" class="btn btn-lg btn-outline-light mt-5 mb-3 mr-1" style="fill:white;">Subscribe for Updates</a>
        <a :href="whitepaperUrl" target="_blank" class="btn btn-lg btn-outline-light mt-5 mb-3 ml-1" >Read Whitepaper</a>
      </div>
      <!-- sm, md -->
      <div class="d-flex d-md-none justify-content-center">
        <a @click="onRegister" :href="subscriptionUrl" target="_blank" class="btn btn-md btn-outline-light mt-5 mb-3 mr-1" style="fill:white;">Subscribe for Updates</a>
        <a @click="onReadWhitepager" :href="whitepaperUrl" target="_blank" class="btn btn-md btn-outline-light mt-5 mb-3 ml-1" >Read Whitepaper</a>
      </div>
    </div>
  </section>
</template>

<script>

export default {
  name: 'Introduction',
  data () {
    return {
      whitepaperUrl: 'https://arxiv.org/pdf/1808.03482.pdf',
      subscriptionUrl: 'https://exeum.us19.list-manage.com/subscribe?u=a412c99ae6213101c313c875c&id=c8204190ae',
      window: {
        width: 0,
        height: 0
      }
    }
  },
  created () {
    window.addEventListener('resize', this.handleResize)
    this.handleResize()
  },
  destroyed () {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    handleResize () {
      this.window.width = window.innerWidth
      this.window.height = window.innerHeight - 73
    },
    onRegister () {
      console.log(this.$ga)
      this.$ga.event({
        eventCategory: 'interest',
        eventAction: 'click',
        eventLabel: 'Subscribe for Updates',
        eventValue: 0
      })
    },
    onReadWhitepager () {
      this.$ga.event({
        eventCategory: 'interest',
        eventAction: 'click',
        eventLabel: 'Read Whitepaper',
        eventValue: 0
      })
    }
  },
  computed: {
    dynamicDiv () {
      return {
        height: `${this.window.height}px`
      }
    }
  }
}
</script>

